body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  font-family: 'Playfair Display', serif;
}

a{
  text-decoration: none;
}

.white{color: #fff;}
.white a{ color: #fff;}
.black{color: #000;}
.black a{ color: #000;}

ul, figure{
  margin: 0;
  padding: 0;
}

ul li{
  list-style: none;
}

header{
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 9;
  margin: 70px 0;
  font-size: 18px;
}

header > a:nth-child(1) span{
  display: block;
  font-size: 24px;
}

header > a:nth-child(1){
  margin-left: 80px;
}

.header_right a:nth-child(1){
  margin-right: 80px;
}

.header_right a:nth-child(2){
  margin-right: 80px;
}

footer{
  display: flex;
  justify-content: space-between;    
  margin: -100px 80px;
  font-size: 18px;
  position: relative;
}

.portfolio_list, .contect_list{
  cursor: pointer;
}

.contect_list{
  text-align: right;
}

.plist p, .clist p{
  margin: 0;
}

.plist, .clist{
  font-size: 16px;
  margin: 5px 0;
}

.plist p, .clist p{
  line-height: 24px;
}

.plist p a, .clist p{
  opacity: 0.6;
}

.plist p a:hover , .clist p:hover {
  opacity: 1;
}

/* onclick */

.clist{
  display: none;
}
.clist.active{
  display: block;
}

.plist{
display: none;
}
.plist.active{
display: block;
}

/* header footer ---------------------------*/

.main_bg img{
  width: 100%;
  height: 100vh;
  vertical-align: top;
}

.set{
  position: absolute;
  top: 50%;
  left: 175px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  color: #fff;
}

.num{
  margin-bottom: 25px;
}

.num span:nth-child(1){
  font-size: 30px;
  margin-right: 10px;
}

.num span:nth-child(2), .num span:nth-child(3){
  font-size: 15px;
  margin-right: 6px;
}

.tit{
  margin-bottom: 20px;
  margin-left: 22px;
}

.tit a{
  font-size: 51px;
  color: rgb(230, 230, 230);
}

.see a{
  font-family: 'IBM Plex Sans KR', sans-serif;
  font-weight: 400;
  margin-left: 24px;
  font-size: 15px;
  opacity: 0.7;
}

.see a:hover{
  opacity: 1;
  text-decoration: underline;
}

.right{
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  right: 38px;
}

.right img{
  width: 105px;
  height: 105px;
}


/* profile css */

.profile_bg{
  background: #000;
  width: 100%;
  height: 100vh;
}

.profile{
  position: absolute;
  top:50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  
}

.i li, .skill li, .skill_text li{
  color: #fff;
  font-family: 'IBM Plex Sans KR', sans-serif;
  font-weight: 200;
  font-size: 15px;
  letter-spacing: 1px;
}


.i{
  margin-right: 200px;
}

.i li:nth-child(1){
  margin-bottom: 43px;
}
.i li:nth-child(2){
  margin-bottom: 43px;
}
.i li:nth-child(3){
  margin-bottom: 5px;
}

.skill_set{
  display: flex;
}

.skill{
  margin-right: 65px;
}

.skill li, .skill_text li{
  margin-bottom: 20px;
}

.skill li:last-child, .skill_text li:last-child{
  margin-bottom: 0;
}

.i li span{
  font-size: 22px;
  font-family: 'IBM Plex Sans KR', sans-serif;
  font-weight: 300;
  padding: 0 8px;
}


