@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@200;300;400&display=swap');


    .detail_all{
        text-align: center;
        font-size: 12px;
        padding: 210px 210px 120px;
    }

    .detail_tit p{
        font-size: 30px;
        font-weight: 500;
    }

    .detail_link p, .detail_text p, .detail_skill p{
        font-family: 'IBM Plex Sans KR', sans-serif;
    }

    .detail_link a{
        text-decoration: underline;
        font-size: 13px;
        padding: 0 10px;
    }

    .detail_text{
        line-height: 8px;
        margin: 50px 0;
    }

    .detail_all > figure{
        margin: 150px auto 100px;
        font-size: 0;
    }

    .detail_all > figure img{
        width: 100%;
        display: block;
        margin-bottom: 19px;
    }

    .top{
        text-align: center;
        padding-bottom: 60px;
        font-size: 18px;
    }

    
    @media (max-width:1222px){
        .detail_all {
            padding: 210px 0 120px;
        }
        .detail_all > figure img{
            width: 82%;
            margin: 0 auto 19px;
        }
    }

    @media (max-width:922px){
        .detail_all > figure {
            margin: 132px auto 100px;
        }
    }

    @media (max-width: 866px){
        .detail_all > figure {
            margin: 112px auto 100px;
        }
    }

    @media (max-width: 811px){
        .detail_all {
            text-align: center;
            font-size: 12px;
            padding: 210px 0 120px;
        }
    }

    @media (max-width:451px){
        .detail_text {
            line-height: 20px;
            margin: 50px auto;
            width: 68%;

        }
        .detail_text p{
            margin: 0;
        }
    }
    
    @media (max-width:443px){
        .detail_all > figure img {
            width: 86%;
        }
    }

    @media (max-width:417px){
        .detail_text {
            width: 80%;
        }
        .detail_skill{
            width: 57%;
            margin: 0 auto;
        }
    }